
*{
  margin: 0;
  padding: 0;
}

li{
  list-style: none;
}

a{
  text-decoration: none;
}

body{
  font-family: 'Montserrat';
  font-weight: normal;
  font-style: normal;
}
:active, :hover, :focus {
    outline: 0;
    outline-offset: 0;
}

.container{
  max-width: 1160px;
  margin: 0 auto;
}

.btn-white{
  font-family: 'Montserrat';
  font-style: normal;
  font-weight: bold;
  font-size: 12px;
  line-height: 15px;
  text-transform: uppercase;
  background-color: #fff;
  color: #BC9CFF;
  border: 2px solid #BC9CFF;
  border-radius: 22px;
  padding: 9px 20px;
  cursor: pointer;
  margin-right: 20px;
  transition: .07s;
}
.btn-gradient,
.btn-gradient-arrow{
  background: linear-gradient(180deg, #BC9CFF 0%, #8BA4F9 100%);
  border-radius: 22px;
  font-family: 'Montserrat';
  font-style: normal;
  font-weight: 700;
  font-size: 12px;
  line-height: 15px;
  text-transform: uppercase;
  color: #FFFFFF;
  border: none;
  padding: 9px 20px;
  cursor: pointer;
  transition: .3s;
}

// .btn-gradient-arrow{
// background: linear-gradient(180deg, #BC9CFF 0%, #8BA4F9 100%);
// border-radius: 22px;
// }

.btn-white:hover,
.btn-gradient:hover,
.btn-gradient-arrow:hover{
  opacity: 0.5;
}

.btn-white:active,
.btn-gradient:active,
.btn-gradient-arrow:active{
  color: rgba(31, 32, 65, 0.5)
}